import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import routes from './router.config.js'
import Header from './pages/Header'
import React from 'react';
import RouteWithSubRoutes from './pages/RouteWithSubRoutes'
const App = () => {
    function PrivatePoute({ component: Com, ...rest }) {
        return (
            <Route
                {...rest}
                render={props =>
                    localStorage.getItem('useinfo')
                        ? <Com {...rest} />
                        : (
                            <Redirect
                                to={{
                                    pathname: "/login",
                                    state: props.location
                                }}
                            />
                        )
                }
            />
        );
    }

    return <Router>
        <Header />
        <Switch>
            {
                routes.map(({ i, pri, ...rest }) => {
                    if (pri) {
                        return <PrivatePoute key={i} {...rest} />
                    }
                    return <RouteWithSubRoutes key={i} {...rest} />
                })
            }
        </Switch>

    </Router>
}

export default App;