import Home from './pages/Home'
import Doc from './pages/Doc'
import Tuto from './pages/Tuto'
import Blog from './pages/Blog'
import Comm from './pages/Comm'
import Login from './pages/Login'
import React from "react";

const routes = [
    {
        path: '/',
        exact: true,
        component: Home,
        pri: false
    },
    {
        path: '/doc',
        exact: false,
        component: Doc,
        pri: true,
        routes: [
            {
                path: '/doc/core',
                component: Core,
            },
            {
                path: '/doc/guide',
                component: Guide,
            },
            {
                path: '/doc/api',
                component: Api,
            },
            {
                path: '/doc/hooks',
                component: Hooks,
            },
        ]
    },
    {
        path: '/tuto',
        exact: false,
        component: Tuto,
        pri: true
    },
    {
        path: '/blog',
        exact: false,
        component: Blog,
        pri: false
    },
    {
        path: '/comm',
        exact: false,
        component: Comm,
        pri: false
    },
    {
        path: '/login',
        exact: false,
        component: Login,
        pri: false
    }
]

function Core(){
    return <h1>Core</h1>
}

function Guide(){
    return <h1>Guide</h1>
}
function Api(){
    return <h1>Api</h1>
}
function Hooks(){
    return <h1>Hooks</h1>
}

export default routes;